/* ----
   Text
   ---- */
.avn-env {
  background-color: #fff;
  text-shadow: 0 1px 0 transparent;
  color: #737373 !important;
  line-height: 1.5em;
  font-family: AvineFontText, Tahoma, Geneva, sans-serif;
}
body.avn-env {
  margin: 0;
}
@media (min-width: 960px) {
  .avn-env {
    font-size: 16px;
  }
}
@media (min-width: 480px) and (max-width: 959px) {
  .avn-env {
    font-size: 14px;
  }
}
@media (max-width: 479px) {
  .avn-env {
    font-size: 13px;
  }
}
.avn-text-margin,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
blockquote,
details,
table,
.avn-table-wrap,
pre,
.avn-text-box {
  margin-top: 1em;
  margin-bottom: 0.4em;
}
.avn-first-child,
h1:first-child {
  margin-top: 0 !important;
}
h1 {
  line-height: 1.15em;
  font-size: 2.5em;
}
h2 {
  line-height: 1.2375em;
  font-size: 2.125em;
}
h3 {
  line-height: 1.325em;
  font-size: 1.75em;
}
h4 {
  line-height: 1.4125em;
  font-size: 1.375em;
}
h5,
p {
  line-height: 1.5em;
  font-size: 1em;
}
h6 {
  line-height: 1.5em;
  font-size: 0.85em;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
}
h1 small,
h2 small,
h3 small,
h4 small,
h5 small,
h6 small {
  line-height: 1em;
  font-size: 0.85em;
}
h6 small {
  font-size: 1em;
}
h2 {
  border-bottom: 1px dotted transparent;
  padding-bottom: 0.125em;
}
hgroup * + h2 {
  border-bottom: initial;
  padding-bottom: auto;
}
hgroup,
hgroup > * {
  margin: 0;
}
hgroup > :first-child {
  margin-top: 1em;
}
hgroup > :last-child {
  margin-bottom: 0.4em;
}
hgroup:first-child > :first-child {
  margin-top: 0;
}
h1,
h3,
h5 {
  color: #737373;
}
h1 small,
h3 small,
h5 small {
  color: #cdcdcd;
}
hgroup h1 + *,
hgroup h3 + *,
hgroup h5 + * {
  color: #cdcdcd;
}
h2,
h4,
h6 {
  color: #6b8a5c;
}
h2 small,
h4 small,
h6 small {
  color: #cdcdcd;
}
hgroup h2 + *,
hgroup h4 + *,
hgroup h6 + * {
  color: #cdcdcd;
}
h2 {
  border-bottom-color: rgba(0, 0, 0, 0.15);
}
a {
  outline: none;
  text-decoration: underline;
}
a:hover,
a:active,
a:focus {
  text-decoration: none;
}
a,
a:link,
a:visited {
  color: #8a5c81;
}
a:hover,
a:active,
a:focus {
  color: #b792af;
}
ol,
ul {
  padding-left: 2em;
}
ul.avn-list-unstyled {
  padding-left: 1em;
  list-style: none;
}
ul.avn-list-nav {
  padding-left: 0;
  list-style: none;
}
ul.avn-list-nav > li {
  border-bottom: 1px dotted rgba(0, 0, 0, 0.15);
  padding: .25em 0;
}
ul.avn-list-nav > li > a {
  display: block;
  text-decoration: none;
}
blockquote {
  margin-left: 1em;
  margin-right: 1em;
  border-left: 4px solid rgba(0, 0, 0, 0.1);
  padding-left: 1em;
  text-align: justify;
  font-style: italic;
}
img {
  vertical-align: middle;
}
a img {
  border: none;
}
hr {
  margin: 1.5em 0;
  border: none;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
details {
  border-radius: 3px;
  border: 1px solid transparent;
  padding: 0.75em 1em;
}
summary {
  outline: none;
}
details[open] {
  border-color: rgba(0, 0, 0, 0.15);
  background-color: rgba(0, 0, 0, 0.05);
}
details[open] summary {
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
}
details,
summary {
  transition: border ease 0.1s, background ease 0.1s;
}
/* text table */
.avn-table-wrap {
  width: 100%;
  overflow: auto;
}
.avn-table-wrap > table {
  margin-top: 0;
  margin-bottom: 0;
}
.avn-table {
  margin-left: auto;
  margin-right: auto;
  border-collapse: collapse;
  border-spacing: 0;
  text-align: left;
}
.avn-table tr {
  vertical-align: top;
  transition: background-color 0.25s ease;
}
.avn-table th {
  background-color: #fff;
}
.avn-table th,
.avn-table td {
  padding: 0.75em 1em;
  border: 1px solid #ddd;
  border-width: 1px 0;
}
.avn-table tr:first-child th,
.avn-table tr:first-child td {
  border-top-width: 0;
}
.avn-table tr:last-child th,
.avn-table tr:last-child td {
  border-bottom-width: 0;
}
table.avn-table.avn-table-bordered th,
table.avn-table.avn-table-bordered td {
  border-width: 1px;
}
table.avn-table th {
  border-bottom-width: 2px /* !important*/;
}
.avn-table-align-left {
  margin-left: initial;
}
.avn-table-align-right {
  margin-right: initial;
}
.avn-table-condensed th,
.avn-table-condensed td {
  padding: 0.5em 0.66666667em;
}
.avn-table-striped tr:nth-child(even) {
  background-color: rgba(0, 0, 0, 0.05);
}
.avn-table-hover tr:hover {
  background-color: rgba(255, 255, 0, 0.15);
}
table.avn-table-valign-top tr > * {
  vertical-align: top;
}
table.avn-table-valign-middle tr > * {
  vertical-align: middle;
}
table.avn-table-valign-bottom tr > * {
  vertical-align: bottom;
}
table tr.avn-table-valign-top > * {
  vertical-align: top;
}
table tr.avn-table-valign-middle > * {
  vertical-align: middle;
}
table tr.avn-table-valign-bottom > * {
  vertical-align: bottom;
}
table tr > .avn-table-valign-top {
  vertical-align: top;
}
table tr > .avn-table-valign-middle {
  vertical-align: middle;
}
table tr > .avn-table-valign-bottom {
  vertical-align: bottom;
}
/* text code */
pre,
code,
samp,
kbd {
  font-family: AvineFontCode, Consolas, Monaco, monospace;
  font-size: .8em;
  line-height: 1.5em;
}
pre,
code {
  tab-size: 4;
}
pre {
  white-space: pre-wrap;
}
code {
  padding: 0px .25em;
  border-radius: 3px;
  border: 1px solid rgba(0, 0, 0, 0.15);
  background-color: rgba(0, 0, 0, 0.05);
  line-height: 1em;
  font-size: 0.85em;
}
pre code {
  display: block;
  padding: .25em .5em;
  font-size: 1em;
  line-height: inherit;
}
pre code.avn-code-unstyled {
  display: inline;
  padding: 0;
  border-radius: 0;
  border: none;
  background-color: transparent;
}
/* text adds */
.avn-text-box {
  position: relative;
  padding: 0.75em 1em;
  border-radius: 5px;
  border: 1px solid #ddd;
  background-color: #fff;
}
.avn-text-box > :first-child {
