  }
  .avn-align-right-medium {
    text-align: right !important;
  }
  .avn-align-justify-medium {
    text-align: justify !important;
  }
}
@media (min-width: 960px) {
  .avn-align-center-large {
    text-align: center !important;
  }
  .avn-align-left-large {
    text-align: left !important;
  }
  .avn-align-right-large {
    text-align: right !important;
  }
  .avn-align-justify-large {
