/* ----
   icon
   ---- */
.avn-icon {
  position: relative;
}
.avn-icon:before,
.avn-icon:after,
.avn-icon-item-before,
.avn-icon-item-after {
  display: none;
  content: none;
  text-decoration: none;
  line-height: 0 !important;
  font-family: FontAwesome;
  font-style: normal;
  font-weight: normal;
  font-size: 1.5em !important;
  color: inherit;
}
.avn-icon-item-before,
.avn-icon-item-after {
  vertical-align: middle;
}
.avn-icon-before:before,
.avn-icon-item-before {
  display: inline;
  margin-right: 0.33333333em;
}
.avn-icon-after:after,
.avn-icon-item-after {
  display: inline;
  margin-left: 0.33333333em;
}
@media (max-width: 719px) {
  .avn-icon-block {
    text-align: center;
  }
  .avn-icon-block:before,
  .avn-icon-block:after,
  .avn-icon-block .avn-icon-item-before,
  .avn-icon-block .avn-icon-item-after {
    width: 1.5em;
    margin: 0 auto;
    line-height: 1em !important;
  }
  .avn-icon-block.avn-icon-before:before,
  .avn-icon-block .avn-icon-item-before {
    display: block;
    margin-bottom: 0.16666667em;
  }
  .avn-icon-block.avn-icon-after:after,
  .avn-icon-block .avn-icon-item-after {
    display: block;
    margin-top: 0.16666667em;
  }
}
@media (min-width: 720px) {
  .avn-icon-block {
    text-align: justify;
  }
  .avn-icon-block:before,
  .avn-icon-block:after,
  .avn-icon-block .avn-icon-item-before,
  .avn-icon-block .avn-icon-item-after {
    margin: 0;
    margin-top: -0.33333333em;
    position: absolute;
    top: 50%;
    line-height: inherit !important;
  }
  .avn-icon-block.avn-icon-before {
    padding-left: 2.5em;
  }
  .avn-icon-block.avn-icon-after {
    padding-right: 2.5em;
  }
  .avn-icon-block.avn-icon-before:before,
  .avn-icon-block .avn-icon-item-before {
    left: 0;
  }
  .avn-icon-block.avn-icon-after:after,
  .avn-icon-block .avn-icon-item-after {
    right: 0;
  }
}
/* icon-small */
.avn-icon-small:before,
.avn-icon-small:after,
.avn-icon-small .avn-icon-item-before,
.avn-icon-small .avn-icon-item-after {
  font-size: 1em !important;
}
@media (min-width: 720px) {
  .avn-icon-small.avn-icon-block:before,
  .avn-icon-small.avn-icon-block:after,
  .avn-icon-small.avn-icon-block .avn-icon-item-before,
  .avn-icon-small.avn-icon-block .avn-icon-item-after {
    margin-top: -0.5em;
  }
  .avn-icon-small.avn-icon-block.avn-icon-before {
    padding-left: 2em;
  }
  .avn-icon-small.avn-icon-block.avn-icon-after {
    padding-right: 2em;
  }
}
/* icon-medium */
.avn-icon-medium:before,
.avn-icon-medium:after,
.avn-icon-medium .avn-icon-item-before,
.avn-icon-medium .avn-icon-item-after {
  font-size: 3em !important;
}
@media (min-width: 720px) {
  .avn-icon-medium.avn-icon-block:before,
  .avn-icon-medium.avn-icon-block:after,
  .avn-icon-medium.avn-icon-block .avn-icon-item-before,
  .avn-icon-medium.avn-icon-block .avn-icon-item-after {
    margin-top: -0.16666667em;
  }
  .avn-icon-medium.avn-icon-block.avn-icon-before {
    padding-left: 4em;
  }
  .avn-icon-medium.avn-icon-block.avn-icon-after {
    padding-right: 4em;
  }
}
/* icon-big */
.avn-icon-big:before,
.avn-icon-big:after,
.avn-icon-big .avn-icon-item-before,
.avn-icon-big .avn-icon-item-after {
  font-size: 4.5em !important;
}
@media (min-width: 720px) {
  .avn-icon-big.avn-icon-block:before,
  .avn-icon-big.avn-icon-block:after,
  .avn-icon-big.avn-icon-block .avn-icon-item-before,
  .avn-icon-big.avn-icon-block .avn-icon-item-after {
