/* ----
   area
   ---- */
.avn-area-bound {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
  min-width: 240px;
  max-width: 960px;
  border-style: none;
  border-color: #ddd;
}
.avn-area-fill {
  position: relative;
  border: 0 solid #ddd;
  border-top-width: 1px;
  background-image: linear-gradient(top, rgba(0, 0, 0, 0) 1px, rgba(0, 0, 0, 0.033) 2px, rgba(0, 0, 0, 0) 9%, rgba(255, 255, 255, 0) 10%, rgba(255, 255, 255, 0.033) 25%, rgba(255, 255, 255, 0) 55%, rgba(0, 0, 0, 0) 56%, rgba(0, 0, 0, 0.033) 85%, rgba(0, 0, 0, 0) 100%);
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.1) inset;
}
.avn-area-nograd.avn-area-fill,
.avn-area-nograd.avn-area-bound > .avn-area-fill {
  background-image: none;
  box-shadow: none;
}
.avn-area-border {
  border-bottom-width: 1px;
}
.avn-area-bound > .avn-area-fill {
  border-color: inherit;
}
@media (min-width: 961px) {
  .avn-area-bound > .avn-area-fill {
    border-left-width: 1px;
    border-right-width: 1px;
  }
  .avn-area-bound > .avn-area-fill:last-child {
    border-bottom-width: 1px;
  }
  .avn-area-bound + .avn-area-bound {
    margin-top: -1px;
  }
  .avn-area-bound.avn-area-spacing {
    margin-top: 1.5em;
  }
  .avn-area-spacing {
    margin-bottom: 1.5em;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.075);
    border-radius: 8px;
  }
  .avn-area-spacing > :first-child {
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
  }
  .avn-area-spacing > :last-child {
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
  }
  .avn-area-noshad {
    box-shadow: none !important;
  }
}
.avn-area-pad,
.avn-area-bound > .avn-area-fill,
.avn-area-fill > .avn-area-bound {
  padding: 0.75em 1em;
}
.avn-area-pad-large {
  padding: 1.25em 1.5em !important;
}
.avn-area-nopad {
  padding: 0 !important;
}
/* area-over */
@media (min-width: 1000px) {
  .avn-area-over {
    margin-left: -10px;
    margin-right: -10px;
    border-width: 1px;
    background-color: #fff;
  }
  .avn-area-over:before,
  .avn-area-over:after {
    border-color: #c4c4c4;
  }
  .avn-area-over > :first-child {
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
  }
  .avn-area-over > :last-child {
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
  }
  .avn-area-over-top {
    border-radius: inherit;
    border-bottom-left-radius: 0 !important;
    border-bottom-right-radius: 0 !important;
  }
  .avn-area-over-top:before,
  .avn-area-over-top:after {
    bottom: -11px;
  }
  .avn-area-over-bottom {
    border-radius: inherit;
    border-top-left-radius: 0 !important;
    border-top-right-radius: 0 !important;
  }
  .avn-area-over-bottom:before,
  .avn-area-over-bottom:after {
    top: -11px;
  }
  .avn-area-over-top:before,
  .avn-area-over-bottom:before,
  .avn-area-over-top:after,
  .avn-area-over-bottom:after {
    content: "\0000a0";
    height: 0;
    width: 0;
    border-width: 10px;
    border-style: solid;
    border-top-color: transparent !important;
    border-bottom-color: transparent !important;
    position: absolute;
  }
  .avn-area-over-top:before,
  .avn-area-over-bottom:before {
    left: -11px;
  }
  .avn-area-over-top:after,
  .avn-area-over-bottom:after {
    right: -11px;
  }
  .avn-area-over-top:before {
    clip: rect(10px, auto, auto, 10px);
  }
  .avn-area-over-top:after {
    clip: rect(10px, 10px, auto, auto);
  }
  .avn-area-over-bottom:before {
    clip: rect(auto, auto, 10px, 10px);
  }
  .avn-area-over-bottom:after {
    clip: rect(auto, 10px, 10px, auto);
  }
  .avn-area-over + .avn-area-fill {
    margin-top: -1px;
  }
}
/* area-over left and right */
@media (min-width: 1000px) {
  .avn-area-over-left,
  .avn-area-over-right {
    box-sizing: border-box;
    position: absolute !important;
    z-index: 1;
    margin: 0 !important;
  }
  .avn-area-over-left {
    left: 1em;
  }
  .avn-area-over-right {
    right: 1em;
  }
  .avn-area-over-bottom.avn-area-over-left,
  .avn-area-over-bottom.avn-area-over-right {
    top: -11px;
    border-radius: 0 0 8px 8px !important;
  }
  .avn-area-over-top.avn-area-over-left,
  .avn-area-over-top.avn-area-over-right {
    bottom: -11px;
    border-radius: 8px 8px 0 0 !important;
  }
  .avn-area-over-bottom.avn-area-over-left:after {
    right: -21px;
    top: -1px;
  }
  .avn-area-over-bottom.avn-area-over-right:before {
    left: -21px;
    top: -1px;
  }
  .avn-area-over-top.avn-area-over-left:after {
    right: -21px;
    bottom: -1px;
  }
  .avn-area-over-top.avn-area-over-right:before {
    left: -21px;
    bottom: -1px;
  }
  .avn-area-over-left:before,
  .avn-area-over-right:after {
    display: none;
  }
  .avn-area-over-wrap-3 .avn-area-over-left,
  .avn-area-over-wrap-3 .avn-area-over-right {
    width: 21em;
  }
  .avn-area-over-main-left-3 {
    margin-left: 22em;
  }
  .avn-area-over-main-right-3 {
    margin-right: 22em;
  }
  .avn-area-over-wrap-2 .avn-area-over-left,
  .avn-area-over-wrap-2 .avn-area-over-right {
    width: 30.5em;
  }
  .avn-area-over-main-left-2 {
    margin-left: 31.5em;
  }
  .avn-area-over-main-right-2 {
    margin-right: 31.5em;
  }
}
.avn-area-over-left,
.avn-area-over-right {
  position: relative;
  margin-top: 1em;
  margin-bottom: 1em;
  padding-left: 1em;
  padding-right: 1em;
  border-radius: 8px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.075);
  border: 1px solid #ddd;
  background-color: #fff;
}
.avn-area-over-bottom.avn-area-over-left:first-child,
.avn-area-over-bottom.avn-area-over-right:first-child {
  margin-top: 0;
}
.avn-area-over-top.avn-area-over-left:last-child,
.avn-area-over-top.avn-area-over-right:last-child {
  margin-bottom: 0;
}
/* over scroll (designed for over left and right) */
.avn-area-over-scroll {
  box-sizing: border-box;
